import { motion, TargetAndTransition, useAnimation } from "framer-motion";
import { useEffect } from "react";

import blobStyles from "./Blob.module.scss";
import { BlobPart, BlobPartCustom } from "./BlobPart";
import { AllBlobSvgValues } from "./BlobSvgValues";

// TODO: see if there is a way to only draw stroke on inside of SVG

export const BlobSvg = () => {
  const blobAnimationControls = useAnimation();

  const animateBlob = async () => {
    await blobAnimationControls.start(
      (blobPartCustom: BlobPartCustom): TargetAndTransition => ({
        pathLength: 0,
        opacity: 0,
        fill: `${blobPartCustom.color}00`,
        strokeWidth: 0.5,
      })
    );

    await blobAnimationControls.start(
      (blobPartCustom: BlobPartCustom): TargetAndTransition => {
        const extraDelay = 0;

        return {
          pathLength: 1,
          opacity: 1,
          fill: `${blobPartCustom.color}ff`,
          strokeWidth: 0,
          transition: {
            pathLength: {
              delay: extraDelay,
              type: "spring",
              duration: 10,
              bounce: 0,
            },
            opacity: { delay: extraDelay, duration: 0.01 },
            fill: {
              delay: extraDelay,
              duration: 1.5,
              ease: "easeIn",
            },
            strokeWidth: { delay: 1.5 + extraDelay, duration: 0.01 },
          },
        };
      }
    );
  };

  useEffect(() => {
    animateBlob();
  }, []);

  return (
    <>
      <div className={blobStyles.blob_container}>
        <motion.svg
          xmlns="http://www.w3.org/2000/svg"
          className={blobStyles.blob}
          viewBox="0 0 299.86 299.86"
        >
          <motion.g transform="translate(182.32 -18.09)">
            {AllBlobSvgValues.map((blobSvgValues, i) => (
              <BlobPart
                key={i}
                blobSvgValues={blobSvgValues}
                index={i}
                animate={blobAnimationControls}
              />
            ))}
          </motion.g>
        </motion.svg>
      </div>
    </>
  );
};
